<style lang="less" scoped>
  .no-data-img {
    text-align: center;
    transform: scale(0.6);
    &>span {
      display: block;
      color: #777;
      font-size: 40px;
    }
  }
  .live_item {
    height: 100%;
    width: 100%;
    margin-top: .5625rem;
  }
  .title {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 30px;
  }
  .course_text_detail_pad {
    padding: 5px 0;
    vertical-align: middle;
    .course_text_detail_pad_img {
      padding-right: 20px;
      vertical-align: middle;
    }
  }
  .course_text_detail>div:nth-child(2) {
    padding: 20px 0 0 0;
  }
  .collection {
    display: block;
    width: 1.25rem;
    height: 1.25rem;
    background-repeat: no-repeat;
    background-size: contain;
  }
  .content_bottom {
    width: 90%;
    height: 110%;
    border-radius: .625rem;
    box-shadow: 0px 2px 15px #ddd;
    margin: 1.25rem auto 0 auto;
  }
  .content_bottom_one {
    margin-top: 0 !important;
  }
  /* 精品课上半部分 START */
  .middel_img_header {
    width: 100%;
    border-radius: .625rem .625rem 0 0;
    overflow: hidden;
  }
  .middel_img_header_text {
    position: absolute;
  }
  .middel_img_header_right_img {
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  /* 精品课上半部分 END */
  /* 精品课下半部分 START */
  .middle_title {
    text-indent: 1em;
  }
  .middle_img_footer {
    display: flex;
  }
  .middle_img_footer .middle_detail {
    -webkit-box-flex: 8;
    flex: 8;
    padding: .3125rem 0 .3125rem 0;
    text-indent: 1em;
  }
  .middle_attention {
    text-indent: 2em;
    img {
      width: 1.5625rem;
    }
  }
  .middle_attention>button {
    border: .0625rem solid #ccc;
    outline: none;
    background-color: transparent;
    border-radius: .625rem;
    width: 3.125rem;
    height: 1.875rem;
  }
  .middle_img_footer .middle_detail>div {
    font-size: 1rem;
    margin-bottom: .0625rem;
  }
  .middle_img_footer .middle_detail>div>span {
    color: #777;
  }
  .middle_img_footer .middle_attention {
    -webkit-box-flex: 3;
    flex: 3;
  }
  .middle_img_footer .middle_attention>a>img {
    margin-top: 20%;
    padding-right: 10%;
    width: 80%;
  }
  /* 精品课下半部分 END */
  /* 课程列表 START */
  .course_list {
    padding-top: .3125rem;
  }
  .course_list>.course_flex {
    justify-content: space-between;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  .flex_middle {
    width: 46%;
    background-color: #fff;
    margin: 10px 0;
    border-radius: 0.266667rem 0.266667rem 0 0;
    overflow: hidden;
  }
  .flex_middle:nth-child(odd){
    margin-left: 20px;
  }
  .flex_middle:nth-child(even){
    margin-right: 20px;
  }
  .flex_middle>div:nth-child(2) {
    padding-left: 20px;
  }
  .course_list .course_flex>div>.course_header_img {
    border-radius: 20px  20px 0 0;
    overflow: hidden;
  }
  .course_list .course_flex>div>.course_header_img>img {
    width: 100%;
    border-radius:  20px  20px 0 0;
  }
  .course_text_detail {
    display: -webkit-flex;
    display: flex;
    margin-top: 10px;
  }
  .course_text_detail>div>p {
    font-size: 30px;
    color: #777;
  }
  .course_text_detail>div:nth-child(1) {
    -webkit-box-flex: 7;
    flex: 7;
    padding-left: 20px;
  }
  .course_text_detail>div:nth-child(2) {
    -webkit-box-flex: 3;
    flex: 3;
    padding-right: 20px;
  }
  .course_text_detail>div:nth-child(2)>button {
    background-color: transparent;
    border: .0625rem solid #555;
    border-radius: 20px;
    width: 3.125rem;
    height: 1.875rem;
    color: #555;
  }
   .live-img{
    vertical-align: middle;
    margin-right: 10px;
    width: 30px;
  }
  /* 课程列表 END */
</style>

<template>
  <div>
    <div v-if="live.length == 0" class="no-data-img">
      <img :src="noDatalist" alt="没有作品哦~">
      <span>暂时没有内容,宝宝先看看别的吧~</span>
    </div>
    <!-- 课程列表 START -->
    <section class="course_list">
      <login v-if="!LOGIN_USER_OBJECT && showLoginModal" v-on:showDialogEvent="showDialogEvent"></login>
      <div class="course_flex">
        <!-- 第一个 -->
        <div class="flex_middle" v-for="(item, index) in live" @click="course_list(item.courseId,item)" :key="index">
          <div class="course_header_img"><img :src="item.coverImage ? item.coverImage : defaultImg" :alt="item.keywords" :title="item.keywords"></div>
          <div>
            <div class="title">{{item.title}}</div>
          </div>
          <div class="course_text_detail">
            <div>
              <p><img class="live-img" :src="item.liveStatus == 'LIVING' ? livingImg : noLiveImg" alt="播放状态">{{item.liveStatus === "LIVING" ? "正在直播" : "暂无直播"}}</p>
              <p class="course_text_detail_pad"><img src="../../../static/images/people1.png" width="20px" class="course_text_detail_pad_img">{{item.studentCount}}</p>
            </div>
            <div class="coll">
              <span class="collection" v-if="0" :style="{backgroundImage: 'url(' + collection + ')'}"></span>
            </div>
          </div>
        </div>
        <!-- 第一个 -->
      </div>
    </section>
  </div>
</template>
<script>
  import login from '@/components/login'
  export default {
    name: 'freePage',
    data() {
      return {
        courses: [],
        collection: require('../../assets/img/heart_g.png'),
        item: {
          courseId: null
        },
        live: [],
        falgNoData: false,
        page: 1,
        pagination: true,
        showData: false,
        noDatalist: require('../../assets/img/da.png'),
        showLoginModal: 0,
        defaultImg: require('../../assets/img/course-large.png'),
        livingImg: require('../../assets/img/open.gif'),
        noLiveImg: require('../../assets/img/hf.png')
      }
    },
    components: {
      login
    },
    created() {
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.axios.get('/api/courses/mylivebroadcast_per', {
        params: {
          id: this.$route.params.id,
          page: this.page
        }
      }).then(res => {
        this.live = res.data.result
        this.$Indicator.close()
        window.onscroll = () => {
          this.dropDown()
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    computed: {
      LOGIN_USER_OBJECT(){
        return this.$store.state.userModule.LOGIN_USER_OBJECT
      }
    },
    methods: {
      showDialogEvent(data) {
        this.showLoginModal = data
      },
      course_list(courseId, item) {
        if (!this.LOGIN_USER_OBJECT) {
          this.showLoginModal = 1
          return
        }
        if(item.liveStatus === 'LIVING'){
          this.$router.push({
            name: 'live',
            params: {
              courseId: courseId,
              id: item.lastCurriculum.liveChannelId
            }
          })
          return
        }
        this.$Indicator.open({
          text: '请求中...',
          spinnerType: 'fading-circle'
        })
        this.axios.get('/api/course', {
          params: {
            courseId: courseId
          }
        }).then(data => {
          if (data.data.isOpened == 'N') {
            this.$Indicator.close()
            this.$Toast({
              message: '未开通该课程',
              position: 'middle',
              duration: 1000
            })
            return
          }
          
          this.axios.get('/api/live', {
            params: {
              courseId: courseId
            }
          }).then(data => {
            this.$Indicator.close()
            if(data.data.result && data.data.result.length > 0){
              data.data.result.forEach((element, index) => {
                if(element && element.courseId && element.liveContentJson.videoContent && element.liveContentJson.videoContent.vid){
                  this.$router.push({
                    name: 'live_vod',
                    params: {
                      id: element.courseId,
                      vid: element.liveContentJson.videoContent.vid
                    }
                  })
                }else if(index === data.data.result.length - 1){
                  this.$Toast({
                    message: '暂时无法观看该课程',
                    position: 'middle',
                    duration: 1000
                  })
                }
              })
            }else{
              this.$Toast({
                message: '暂时无法观看该课程',
                position: 'middle',
                duration: 1000
              })
            }
          }).catch(err => {
            console.log(err)
          })


        }).catch(err => {
          console.log(err)
        })
      },
      dropDown() {
        if (this.falgNoData) { //数据全部加载完了
          return;
        } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行
          this.pagination = false
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })
          this.page += 1
          this.axios.get('/api/courses/mylivebroadcast_per', {
            params: {
              id: this.$route.params.id,
              page: this.page
            }
          }).then((res) => {
            if (res.data.result && res.data.result.length > 0) {
              this.pagination = true
              this.live = this.live.concat(res.data.result)
              this.$Indicator.close()
            } else {
              this.$Indicator.close()
              this.$Toast({
                message: '没有更多了~',
                position: 'middle',
                duration: 1000
              })
            }
          }).catch((err) => {
            console.log(err);
          });
        }
      },
    }
  }
</script>



